"use client"

import { useAuth } from "@/lib/auth-context"
import MainLayout from "@/components/layout/main-layout"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { LayoutDashboard, Calendar, Bluetooth as Tooth, BarChart3, CheckCircle, XCircle, DollarSign } from "lucide-react"
import { useState, useEffect } from "react"
import { appointmentService, dentistService, paymentService, treatmentService } from "@/lib/db-service"
import CollectPaymentModal from "@/components/modals/collect-payment-modal"

interface Appointment {
  id: string
  patient_id: string
  dentist_id: string
  date: string
  time: string
  status: string
  treatment_id?: string
  service?: string
  treatment?: string
  amount?: number
  notes?: string
  patients?: {
    name: string
    email: string
  }
  dentists?: {
    name: string
  }
}

export default function DentistDashboard() {
  const { user } = useAuth()
  const [appointments, setAppointments] = useState<Appointment[]>([])
  const [dentistTableId, setDentistTableId] = useState<string | null>(null)
  const [loading, setLoading] = useState(true)
  const [processingId, setProcessingId] = useState<string | null>(null)

  useEffect(() => {
    loadData()
  }, [user?.email])

  const loadData = async () => {
    if (!user?.email) return
    try {
      // Map auth user -> dentist table row (prefer user_id, fallback email)
      const dentists = await dentistService.getAll()
      const matchByUser = (dentists || []).find((d: any) => d.user_id === user.id)
      const matchByEmail = (dentists || []).find(
        (d: any) => (d.email || "").toLowerCase() === (user.email || "").toLowerCase(),
      )
      const match = matchByUser || matchByEmail
      if (match?.id) {
        setDentistTableId(match.id)
        await loadAppointments(match.id)
      } else {
        setDentistTableId(null)
        setAppointments([])
      }
    } catch (err) {
      console.error("[v0] Error loading dentist record:", err)
      setAppointments([])
    }
  }

  const loadAppointments = async (dentistId?: string) => {
    const idToUse = dentistId || dentistTableId
    if (!idToUse) return
    try {
      setLoading(true)
      const data = await appointmentService.getByDentistId(idToUse)
      setAppointments(data || [])
    } catch (error) {
      console.error("[v0] Error loading appointments:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleApproveAppointment = async (appointmentId: string) => {
    setProcessingId(appointmentId)
    try {
      await appointmentService.changeStatus(appointmentId, "confirmed")
      setAppointments((prev) => prev.map((apt) => (apt.id === appointmentId ? { ...apt, status: "confirmed" } : apt)))
    } catch (error) {
      console.error("[v0] Error approving appointment:", error)
      alert("Failed to approve appointment")
    } finally {
      setProcessingId(null)
    }
  }

  const handleCompleteAppointment = async (appointment: Appointment) => {
    setProcessingId(appointment.id)
    try {
      // Mark appointment as completed first
      await appointmentService.changeStatus(appointment.id, "completed")
      setAppointments((prev) => prev.map((apt) => (apt.id === appointment.id ? { ...apt, status: "completed" } : apt)))
      
      // Calculate price
      let amount = appointment.amount || 0
      
      // Try to get price from treatment_id
      if (!amount && appointment.treatment_id) {
        try {
          const treatment = await treatmentService.getById(appointment.treatment_id)
          amount = treatment?.price || 0
        } catch (e) {
          console.warn("[v0] Could not resolve treatment by id:", e)
        }
      }
      
      // Try to match by service name
      if (!amount) {
        try {
          const treatments = await treatmentService.getAll()
          const matched = (treatments || []).find((t: any) => t.name === (appointment.service || appointment.treatment))
          amount = matched?.price || 0
        } catch (e) {
          console.warn("[v0] Could not lookup treatments:", e)
        }
      }
      
      // Use default prices based on service name
      if (!amount) {
        const defaultPrices: Record<string, number> = {
          "Cleaning": 500, "Tooth Cleaning": 500, "Dental Cleaning": 500,
          "Filling": 800, "Dental Filling": 800, "Tooth Filling": 800,
          "Root Canal": 1500, "Root Canal Treatment": 1500,
          "Extraction": 600, "Tooth Extraction": 600,
          "Whitening": 2000, "Teeth Whitening": 2000,
          "Checkup": 300, "Dental Checkup": 300, "Consultation": 300,
          "Braces": 3000, "Dental Braces": 3000,
          "Crown": 2500, "Dental Crown": 2500,
        }
        amount = defaultPrices[appointment.service] || 500
      }
      
      console.log("[v0] Calculated amount:", amount, "for service:", appointment.service)
      
      // Show collect payment modal
      setSelectedAppointment(appointment)
      setCalculatedAmount(amount)
      setShowCollectModal(true)
    } catch (error) {
      console.error("[v0] Error completing appointment:", error)
      alert("Failed to complete appointment")
    } finally {
      setProcessingId(null)
    }
  }

  const handleCollectPayment = async (paymentData: { method: string; amount: number; notes?: string }) => {
    if (!selectedAppointment) return

    try {
      console.log("[v0] Collecting payment:", paymentData)
      
      const paymentPayload = {
        patient_id: selectedAppointment.patient_id,
        dentist_id: selectedAppointment.dentist_id,
        appointment_id: selectedAppointment.id,
        amount: paymentData.amount,
        method: paymentData.method,
        status: "paid",
        description: `${selectedAppointment.service || "treatment"} - ${paymentData.notes || "Payment collected"}`,
        date: new Date().toISOString().split("T")[0],
      }
      
      const createdPayment = await paymentService.create(paymentPayload)
      console.log("[v0] âœ… Payment saved to database:", createdPayment)
      
      // Close modal and show success
      setShowCollectModal(false)
      setSelectedAppointment(null)
      
      alert(
        `âœ… PAYMENT COLLECTED SUCCESSFULLY!\n\n` +
        `ðŸ’µ Amount: â‚±${paymentData.amount.toLocaleString()}\n` +
        `ðŸ’³ Method: ${paymentData.method.replace(/_/g, " ").toUpperCase()}\n` +
        `ðŸ‘¨â€âš•ï¸ Your Share (50%): â‚±${(paymentData.amount * 0.5).toLocaleString()}\n\n` +
        `ðŸ“Š Payment ID: ${createdPayment.id}\n\n` +
        `âœ“ Recorded in database\n` +
        `âœ“ Will appear in your Earnings\n` +
        `âœ“ Visible to HR in Payments section`
      )
      
      // Reload appointments to update stats
      loadAppointments()
    } catch (error) {
      console.error("[v0] Error recording payment:", error)
      const errorMsg = error instanceof Error ? error.message : String(error)
      alert(`âŒ Payment collection failed!\n\nError: ${errorMsg}\n\nPlease try again or contact support.`)
    }
  }

  const handleRejectAppointment = async (appointmentId: string) => {
    setProcessingId(appointmentId)
    try {
      await appointmentService.changeStatus(appointmentId, "rejected")
      setAppointments((prev) => prev.map((apt) => (apt.id === appointmentId ? { ...apt, status: "rejected" } : apt)))
    } catch (error) {
      console.error("[v0] Error rejecting appointment:", error)
      alert("Failed to reject appointment")
    } finally {
      setProcessingId(null)
    }
  }



  const pendingAppointments = appointments.filter((apt) => apt.status === "pending")
  const approvedAppointments = appointments.filter((apt) => apt.status === "confirmed" || apt.status === "attended")
  const todayAppointments = approvedAppointments.filter((apt) => {
    const today = new Date().toISOString().split("T")[0]
    return apt.date === today
  })

  const navItems = [
    { label: "Dashboard", icon: <LayoutDashboard className="w-5 h-5" />, href: "/dentist/dashboard" },
    { label: "My Schedule", icon: <Calendar className="w-5 h-5" />, href: "/dentist/schedule" },
    { label: "Treatments", icon: <Tooth className="w-5 h-5" />, href: "/dentist/treatments" },
    { label: "Earnings", icon: <DollarSign className="w-5 h-5" />, href: "/dentist/earnings" },
    { label: "Reports", icon: <BarChart3 className="w-5 h-5" />, href: "/dentist/reports" },
  ]

  return (
    <MainLayout navItems={navItems} title="Dentist Schedule">
      <div className="space-y-6">
        {/* Welcome Section */}
        <div className="bg-gradient-to-r from-primary to-accent rounded-lg p-6 text-primary-foreground">
          <h1 className="text-3xl font-bold mb-2">Welcome, Dr. {user?.name}</h1>
          <p className="opacity-90">Manage your appointments and patient treatments</p>
        </div>

        {/* Quick Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <Card className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">Today's Appointments</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-primary">{todayAppointments.length}</div>
              <p className="text-xs text-muted-foreground mt-1">All confirmed</p>
            </CardContent>
          </Card>

          <Card className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">Pending Approvals</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-yellow-600">{pendingAppointments.length}</div>
              <p className="text-xs text-muted-foreground mt-1">Awaiting action</p>
            </CardContent>
          </Card>

          <Card className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">This Week</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-primary">{approvedAppointments.length}</div>
              <p className="text-xs text-muted-foreground mt-1">Total appointments</p>
            </CardContent>
          </Card>

          <Card className="hover:shadow-md transition-shadow">
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-muted-foreground">Completed Today</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-green-600">
                {appointments.filter((apt) => apt.status === "completed").length}
              </div>
              <p className="text-xs text-muted-foreground mt-1">Completed</p>
            </CardContent>
          </Card>
        </div>

        {/* Pending Approvals */}
        <Card>
          <CardHeader>
            <CardTitle>Pending Appointment Approvals</CardTitle>
            <CardDescription>Appointments awaiting your decision</CardDescription>
          </CardHeader>
          <CardContent>
            {loading ? (
              <div className="text-center py-8 text-muted-foreground">Loading appointments...</div>
            ) : pendingAppointments.length === 0 ? (
              <div className="text-center py-8 text-muted-foreground">No pending appointments</div>
            ) : (
              <div className="space-y-3">
                {pendingAppointments.map((apt) => (
                  <div
                    key={apt.id}
                    className="flex items-center justify-between p-4 border border-border rounded-lg hover:bg-muted/50 transition-colors"
                  >
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                        <span className="text-sm font-semibold text-primary">
                          {apt.patients?.name ? apt.patients.name.charAt(0) : "?"}
                        </span>
                      </div>
                      <div>
                        <p className="font-medium text-foreground">{apt.patients?.name || "Unknown Patient"}</p>
                        <p className="text-sm text-muted-foreground">
                          {apt.date} at {apt.time}
                        </p>
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button
                        size="sm"
                        className="bg-green-600 hover:bg-green-700 text-white"
                        onClick={() => handleApproveAppointment(apt.id)}
                        disabled={processingId === apt.id}
                      >
                        <CheckCircle className="w-4 h-4 mr-1" />
                        {processingId === apt.id ? "..." : "Accept"}
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        className="border-destructive text-destructive hover:bg-destructive/10 bg-transparent"
                        onClick={() => handleRejectAppointment(apt.id)}
                        disabled={processingId === apt.id}
                      >
                        <XCircle className="w-4 h-4 mr-1" />
                        {processingId === apt.id ? "..." : "Reject"}
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Approved / Attended */}
        <Card>
          <CardHeader>
            <CardTitle>Approved / Attended</CardTitle>
            <CardDescription>Complete after visit</CardDescription>
          </CardHeader>
          <CardContent>
            {approvedAppointments.length === 0 ? (
              <div className="text-center py-8 text-muted-foreground">No approved/attended appointments</div>
            ) : (
              <div className="space-y-3">
                {approvedAppointments.map((apt) => (
                  <div
                    key={apt.id}
                    className="flex items-center justify-between p-4 border border-border rounded-lg hover:bg-muted/50 transition-colors"
                  >
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 rounded-full bg-primary/10 flex items-center justify-center">
                        <span className="text-sm font-semibold text-primary">
                          {apt.patients?.name ? apt.patients.name.charAt(0) : "?"}
                        </span>
                      </div>
                      <div>
                        <p className="font-medium text-foreground">{apt.patients?.name || "Unknown Patient"}</p>
                        <p className="text-sm text-muted-foreground">
                          {apt.date} at {apt.time}
                        </p>
                        {apt.status === "attended" && (
                          <p className="text-xs text-blue-600">Patient marked as attended</p>
                        )}
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button
                        size="sm"
                        className="bg-green-600 hover:bg-green-700 text-white"
                        onClick={() => handleCompleteAppointment(apt)}
                        disabled={processingId === apt.id}
                      >
                        <CheckCircle className="w-4 h-4 mr-1" />
                        {processingId === apt.id ? "..." : "Complete"}
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Today's Schedule */}
        <Card>
          <CardHeader>
            <CardTitle>Today's Schedule</CardTitle>
            <CardDescription>Your confirmed appointments for today</CardDescription>
          </CardHeader>
          <CardContent>
            {todayAppointments.length === 0 ? (
              <div className="text-center py-8 text-muted-foreground">No appointments today</div>
            ) : (
              <div className="space-y-3">
                {todayAppointments.map((apt) => (
                  <div key={apt.id} className="flex items-center justify-between p-3 border border-border rounded-lg">
                    <div className="flex items-center gap-3">
                      <div className="flex-1">
                        <p className="font-medium text-foreground">{apt.patients?.name || "Unknown Patient"}</p>
                        <p className="text-sm text-muted-foreground">{apt.time}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <span className="px-2 py-1 bg-green-100 text-green-700 text-xs rounded-full">Confirmed</span>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

      </div>
    </MainLayout>
  )
}
